.radio {
    appearance: none;
    height: 12px;
    width: 12px;
    cursor: pointer;
    border-radius: 50%;
    outline: none;
    background: blue;
}

.radio:hover {
    border: 2px solid red;
}

.radio:checked {
    background: red;
}

.radio:focus {
    border: 2px solid red;
}